<template>
  <v-container grid-list-xl class="mt-5 pt-5 users">
    <v-layout row wrap v-if="$store.state.users" class="animate-opacity">
      <v-flex v-for="user in $store.state.users" :key="user.id" lg6 offset-lg3 md8 offset-md2 xs12 class="user-card">
        <v-card class="pa-3">
          <v-layout :column="$vuetify.breakpoint.smAndDown">

            <router-link :to="{ name: 'user', params: { user_id: user.id } }">
              <img src="./../../public/no-person.jpg" class="image-container">
           </router-link>
            <v-card-title primary-title class="card-title mb-2" :class='{"py-0 mt-3": $vuetify.breakpoint.smAndDown}'>
                <h3><router-link :to="{ name: 'user', params: { user_id: user.id } }">{{ user.firstName }} {{ user.lastName }}</router-link></h3>
            </v-card-title>
            <v-spacer/>
            <v-card-actions >
              <v-btn class="mr-3 btn2" :to="{ name: 'user', params: { user_id: user.id } }">View profile</v-btn>
            </v-card-actions>
          </v-layout>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<style lang="sass">

$smartphone-width: 960px

.users *
  color: #3f51b5

.card-title *
  color: #3f51b5 !important

.user-card .btn1 div
  color: #fff !important

.user-card .btn2 div
  color: #3f51b5 !important

.image-container
  width: 50px
  height: 50px
  margin: 12px 0 12px 25px
  @media screen and (max-width: $smartphone-width)
    margin: 10px 0 0 15px

.animate-opacity
  animation: opacity-anim .5s forwards ease-in-out

@keyframes opacity-anim
  from
    transform: translateY(-200px)
    opacity: 0

  to
    transform: translateY(0px)
    opacity: 1

  
</style>

<script>
import db from "@/firebase/init"
import './../../node_modules/nprogress/nprogress.css'
import NProgress from 'nprogress'

export default {
  created () {
    NProgress.start()
    db.collection('users').get().then((querySnapshot) => {
    let users = []
    querySnapshot.forEach((doc) => {
        let user = doc.data()
        users.push(user)
    })
    this.$store.state.users = users
    }).then(() => {
      NProgress.done()
    })
  }
}
</script>
